@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');

@keyframes infinite_bounce_logo {
    0% {
        transform: scale(100%);
    }

    100% {
        transform: scale(110%);
    }
}

@keyframes infinite_cube {
    0% {
        transform: rotateY(0) rotateX(0);
    }
    50%{
        transform: rotateY(-180deg) rotateX(180deg);
    }

    100% {
        transform: rotateY(-360deg) rotateX(360deg);
    }
}

@keyframes infinite_galley {
    0% {
        transform: rotateY(0);
    }
    100% {
        transform: rotateY(-360deg) ;
    }
}

body {
    margin: 0;
    font-family: 'Nanum Gothic', sans-serif;
    overflow-x: hidden;
}

.logo {
    overflow: hidden;
}

.logo img {
    display: flex;
    justify-content: center;
    height: 50px;
    margin: auto;
    padding: auto;
    animation: infinite_bounce_logo 1s infinite linear;
    padding:5px 0px 5px 0px;
}
.header_container{
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding:0px 60px 0px 60px;
    border-bottom: 1px solid #abacb1;
    background-color: white;
}

.header_container div{
    font-size:14px;
    font-weight: bold;
    cursor:pointer;
}

.header_nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    background-color: #f5f5f7;
    height: 60;
}

.header_nav a {
    text-decoration: none;
}

.header_nav li {
    color: #232323;
    padding: 20px 20px 20px 20px;
    font-size: 13px;
    font-weight:bold;
    cursor: pointer;
}

.header_nav li:hover {
    font-weight: bold;
    text-shadow: 3px 3px 10px #252a2c;
    transition: all 0.3s;
}

.collection{
    position: relative;
}

.collection_container{
    display:flex;
    position:absolute;
    padding:75px 0px 75px 0px;
    background-color: #252525;
    z-index:2;
    transform: translateX(-100%);
}

.collection_container div{
    width:460px;
    height:650px;
    margin:0px 10px 0px 10px;
    cursor: pointer;
}

.collection_container img{
    width:460px;
    height:650px;
}

.collection_delete{
    display:none;
    position:absolute;
    top:20px;
    right:20px;
    fill : white;
    z-index:5;
    cursor: pointer;
}

.collection_delete svg{
    font-size:30px;
}

.collcection_btn{
    position: absolute;
    z-index:3;
}

