.gallery_container {
    width: 100%;
    perspective: 1500px;
}

.gallery{
    position: relative;
    margin:auto;
    width:1600px;
    display:flex;
    justify-content: center;
    transform-style: preserve-3d;
    animation: infinite_galley 20s infinite linear;
    top:145px;
}

.gallery img{
    position: absolute;
    width:150px;
    height: 200px;
    transform: rotateY(calc(var(--incline) * 1deg)) translateZ(450px);
    z-index:-1;
}

.gallery_one{
